.App {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 2em;
}

.App .Welcome {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 0.2em;
  -webkit-box-shadow: 0 1em 2em -1em #000;
          box-shadow: 0 1em 2em -1em #000;
  margin: 4em;
  max-width: 800px;
  padding: 2em;
}

.App .Welcome.blur {
  -webkit-filter: blur(6px);
          filter: blur(6px);
}

.App .Welcome .question {
  margin: 2em 0;
}

.App .Popup {
  background: #fff;
  -webkit-box-shadow: 0 1em 2em -1em #000;
          box-shadow: 0 1em 2em -1em #000;
  position: absolute;
  max-width: 30em;
  margin-bottom: 5em;
  padding: 2em;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -o-transition: transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  z-index: 2;
}

.App .Popup .close svg {
  cursor: pointer;
  float: right;
  height: 50px;
  width: 50px;
}

.App .Popup .logo svg {
  display: block;
  margin: 1em auto;
  height: 100px;
  width: 100px;
}

.App .Popup.hidden {
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  pointer-events: none;
}



